import React from "react";
import Link from "gatsby-link";

const ProjectCard = () => (
  <div
    style={{
      borderRadius: "4px",
      width: 300,
      height: 450,
      padding: 0,
      margin: 3,
      boxShadow: "0 2px 5px rgba(0, 0, 0, 0.2)"
    }}
  >
    <img
      src="https://avatars0.githubusercontent.com/u/33296533?s=400&amp;v=4"
      alt="Preview"
      style={{
        borderRadius: "4px 4px 0px 0px",
        margin: 0,
        maxWidth: "100%",
        maxHeight: 300
      }}
    />
    <h6 style={{ paddingLeft: 10, paddingTop: 10 }}>Title of Project</h6>
    <p style={{ color: "gray", paddingLeft: 10 }}>
      This is a short excert with a quick project overview.
    </p>
  </div>
);

export default ProjectCard;



